body {
  animation-delay: 1s;
  animation-duration: 1s;
  animation-fill-mode: backwards;
  animation-name: bgLaunch;
  animation-timing-function: ease-in;
  background-color: #f3f7fa;
  font-family: "Open Sans", sans-serif;
  height: inherit; }

